import React, {useState} from "react";
import styled from "styled-components";

const FullButton = styled.button`
  background: ${(props) => (props.type === "basic" ? "#6A8BFF" : "#ffffff")};
  color: ${(props) => (props.type === "basic" ? "#ffffff" : "#000000")};
  border: ${(props) => (props.type === "basic" ? "none" : "1px solid #000000")};
  border-radius: 8px;
  display: block;
  width: 100%;
  height: ${(props) => props.height};
  box-shadow: 0px 0px 10px rgba(0, 28, 145, 0.2);
  font-weight: 500;
  font-size: 1em;
  line-height: 1.25em;
  letter-spacing: -0.07em;
  text-align: center;

  /* &:hover {
    box-shadow: 0 80px 0 0 rgba(0, 0, 0, 0.25) inset,
      0 -80px 0 0 rgba(0, 0, 0, 0.25) inset;
  } */

  &:active {
    background: ${(props) => (props.type === "basic" ? "#021248" : "#ffffff")};

  }
`;
// height : em, forn-size : ë°˜ì‘í˜•
const Button = ({ children, type, height, ...rest }) => {

  return (
    <FullButton type={type} height={height}{...rest}>
      {children}
    </FullButton>
  );
};

export default Button;
