const BalanceGameSelect = () => {
  const navigate = useNavigate();
  const [searchParams, setSearchParams] = useSearchParams();
  const [userId, setUserId] = useState("");
  const [jwt, setJwt] = useState("");
  // const [rnSend, setRnSend] = useState(false);
  const [isApp, setIsApp] = useState(null);
  const [platform, setPlatform] = useState(null);
  const [balanceGame, setBalanceGame] = useState({});
  const [loading, setLoading] = useState(true);

  const pathId = searchParams.get("id");

  useEffect(() => {
    window.ReactNativeWebView?.postMessage(JSON.stringify(true));
    //android
    document.addEventListener("message", (e) => {
      try {
        const parseData = JSON.parse(e.data);
        if (parseData.loginData) {
          setUserId(parseData.loginData.id);
          setJwt(parseData.loginData.jwt);
        }
        // setRnSend(true);
        setIsApp(parseData.isApp);
        setPlatform(parseData.platform);
      } catch (err) {
        console.log(err);
      }
    });
    //ios
    window.addEventListener("message", (e) => {
      try {
        const parseData = JSON.parse(e.data);
        if (parseData.loginData) {
          setUserId(parseData.loginData.id);
          setJwt(parseData.loginData.jwt);
        }
        // setRnSend(true);
        setIsApp(parseData.isApp);
        setPlatform(parseData.platform);
      } catch (err) {
        console.log(err);
      }
    });
    getData();
  }, []);

  const getData = async () => {
    setLoading(true);
    // console.log("hi");
    await axios({
      method: "get",
      headers: {
        "Access-Control-Allow-Credentials": true,
        "ngrok-skip-browser-warning": true,
      },
      url: `${process.env.REACT_APP_API_URL}/balancegame/start?balanceGameId=${pathId}`,
    })
      .then((res) => {
        setBalanceGame(res.data.balanceGame[0]);
        setLoading(false);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const selectEvent = async (gameId, nameId, check) => {
    await axios({
      method: "post",
      url: `${process.env.REACT_APP_API_URL}/balancegame/saveResult1`,
      headers: {
        Authorization: jwt,
      },
      data: {
        balanceGameId: gameId,
        balanceNameId: nameId,
      },
    })
      .then((res) => {
        navigate("/balancegameselectresult", {
          state: {
            check: check,
            jwt: jwt,
            userId: userId,
            isApp: isApp,
            platform: platform,
            pathId: pathId,
            guest_name: res.data.guest_name,
          },
        });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <Container>
      {loading ? (
        <LoadingBox>
          <ClipLoader color="#25B1FA" />
        </LoadingBox>
      ) : (
        <>
          <TextContainer>
            <TextTopContainer>
              <WhiteText>{balanceGame?.title}</WhiteText>
            </TextTopContainer>
          </TextContainer>
          <SelectContainer>
            <SelectTopContainer
              onClick={() =>
                selectEvent(balanceGame.balanceGameId, balanceGame.balanceInfo[0].balanceNameId, [
                  true,
                  false,
                ])
              }
            >
              <SelectImage src={balanceGame?.balanceInfo[0].balanceImage} />
              <SelectBtn>{balanceGame?.balanceInfo[0].balanceName}</SelectBtn>
            </SelectTopContainer>
            <VersusImgage src={versusImage} />
            <SelectBotContainer
              onClick={() =>
                selectEvent(balanceGame.balanceGameId, balanceGame.balanceInfo[1].balanceNameId, [
                  false,
                  true,
                ])
              }
            >
              <SelectImage src={balanceGame?.balanceInfo[1].balanceImage} />
              <SelectBtn>{balanceGame?.balanceInfo[1].balanceName}</SelectBtn>
            </SelectBotContainer>
          </SelectContainer>
        </>
      )}
    </Container>
  );
};

export default BalanceGameSelect;

